/*Responsive Header*/
.header-res {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #dbc0ab;
    height: 100vh;
    z-index: 2;
    margin-right: -300px;
    transition-duration: 0.5s;
    box-shadow: 0px 2px 15px -1px rgb(0 0 0 / 57%);
}
.btn-hc, .btn-ho {
    cursor: pointer;
    width: 30px;
    height: 30px;
    background-size: contain;
}
.btn-hc{
    position: absolute;
    z-index: 3;
    margin-left: -50px;
    margin-top: 32px;
}
.header-res-stat {
    display: none;
    justify-content: space-between;
    align-items: center;
}
.hide{
    display: none !important;
}
.header-res ul{
    display: block;
}
.header-res ul li{
    padding: 20px 70px 20px 30px;
}
.header-res ul li:hover{
    background-color: #fff;
    color: #000;
}
.header-res-active{
    margin-right: 0;
    transition-duration: 0.5s;  
}
.header-res .header-res-logo li:nth-child(1) {
    padding: 0;
}
